// stylelint-disable selector-max-specificity

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
  color: $steel;
}

input[type='text'],
input[type='tel'],
input[type='email'],
input[type='search'],
input[type='password'],
input[type='url'],
input[type='date'],
textarea {
  -webkit-appearance: none;
}

select {
  -webkit-appearance: none;
}

button,
input[type='button'],
input[type='reset'],
input[type='file'],
input[type='submit'] {
  -webkit-appearance: none;
  cursor: pointer;
}

body.home {
  background: $white;
}

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 11px 8px;
  font-size: 16px;
  line-height: 1.2;
  font-weight: 600;
  transition: all 0.3s linear;
  color: $white;
  text-decoration: none;
  text-align: center;

  @include breakpoints(s-medium) {
    padding: 11px 21px;
  }

  &.btn-orange {
    transition: background 0.3s linear;
    background: $poppy;

    &:hover {
      background: $poppy;
    }
  }

  &.btn-black {
    transition: background 0.3s linear;
    background: $black;

    &:hover {
      background: #111111;
    }
  }

  &.btn-dark-blue {
    transition: background 0.3s linear;
    background: $deep_ocean;

    &:hover {
      background: darken($deep_ocean, 10);
    }
  }

  &.btn-future-blue {
    transition: background 0.3s linear;
    background: $future_blue;

    &:hover {
      background: darken($future_blue, 10);
    }
  }

  &.btn-white {
    transition: background 0.3s linear;
    background: $white;
    color: $future_blue;

    &:hover {
      background: darken($white, 10);
    }
  }

  &.btn-green {
    background: $avocado;

    &:hover {
      background: darken($avocado, 15%);
    }
  }
}

.link {
  display: inline-block;
  vertical-align: top;
  color: $poppy;
  font-size: rem(15);
  padding-right: 22px;
  position: relative;
  text-decoration: none;
  transition: all 0.3s linear;

  &::after {
    content: "\e902";
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    font: 13px/1 "icomoon";
    margin-top: -1px;
  }

  &:hover {
    color: darken($poppy, 10%);
  }
}

.all-link {
  transition: color 0.3s linear;
  text-decoration: none;
  font-size: rem(14);
  line-height: 1;
  font-weight: 300;
  color: $black;
  margin-bottom: rem(6);

  @include breakpoints(s-medium) {
    font-size: rem(16);
  }

  &:hover {
    color: $poppy;
  }
}

.home-main {
  padding: rem(46 0);

  @include breakpoints(s-medium) {
    padding: rem(25 0);
  }

  @include breakpoints(s-large) {
    padding: rem(25 0 103);
  }

  > .container {
    @include breakpoints(s-large) {
      display: flex;
      flex-wrap: wrap;
    }
  }

  .aside {
    @include breakpoints(s-large) {
      width: 380px;
    }

    @include breakpoints(m-large) {
      width: 418px;
    }

    .widget-box {
      .order {
        @include breakpoints(s-medium) {
          transition: transform 0.25s ease-in-out, box-shadow 0.25s ease-in-out;
          will-change: transform;
          backface-visibility: hidden;

          .img-wrap {
            overflow: hidden;

            img {
              display: block;
              position: relative;
              transition: transform 0.25s ease-in-out;
              will-change: transform;
              backface-visibility: hidden;
            }
          }

          &:hover {
            transform: translate3d(0, -4.5px, 0);
          }
        }
      }
    }
  }

  .post {
    @include breakpoints(s-medium) {
      transition: transform 0.25s ease-in-out, box-shadow 0.25s ease-in-out;
      //will-change: transform;
      //backface-visibility: hidden;
      font-smoothing: antialiased;
      filter: blur(0);
      transform: translateZ(0.00001px);

      .img-frame {
        a {
          display: block;
          position: absolute;
          left: 0;
          right: 0;
          top: 0;
          bottom: 0;
        }

        .img {
          z-index: 1;
          image-rendering: optimize-contrast;
          filter: blur(0);
        }
      }

      //&:hover {
      //  .img {
      //    transform: scale(1.1) translateZ(0.00001px);
      //  }
      //}
    }
  }
}

#content-col {
  margin-bottom: rem(64);

  @include breakpoints(s-large) {
    flex: 1;
    padding: rem(0 50 0 0);
    margin-bottom: 0;
  }

  @include breakpoints(m-large) {
    padding: rem(0 140 0 0);
  }

  .top-row {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    border-bottom: 1px solid $concrete;
    padding-bottom: rem(13);
    margin-bottom: rem(32);

    @include breakpoints(s-medium) {
      min-height: 60px;
      margin-bottom: rem(32);
    }

    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      font-size: rem(28);
      line-height: 1.22;
      font-weight: 300;
      margin: 0 -16px;
      display: flex;
      align-items: flex-end;

      @include breakpoints(s-medium) {
        margin: 0 -24px;
      }

      @include breakpoints(m-large) {
        font-size: rem(36);
      }

      li {
        padding: 0 16px;

        @include breakpoints(s-medium) {
          padding: 0 24px;
        }

        &.active a {
          color: $poppy;
        }

        a {
          transition: color 0.3s linear;
          color: $fog;
          text-decoration: none;

          &:hover {
            color: $poppy;
          }
        }
      }
    }
  }

  .widget-box {
    margin-bottom: rem(30);
  }
}

#content-col {

  .post-list--posts > .post {
    padding-bottom: 0;
  }
}

.post {
  margin-bottom: rem(32);
  padding: 0;
  border: 1px solid $fog;

  @include breakpoints(s-medium) {
    margin-bottom: rem(23);
  }

  > .post-wrap,
  > a {
    display: flex;
    flex-wrap: wrap;
    text-decoration: none;
    color: $concrete;
  }

  .img-wrap {
    display: flex;
    width: 104px;
    position: relative;
    overflow: hidden;
    //padding: 24px 0 24px 25px;

    @include breakpoints(s-medium) {
      width: 198px;
    }

    @include breakpoints(m-large) {
      width: 238px;
    }

    .img-frame {
      width: 100%;
      padding-top: 100%;

      @include breakpoints(s-medium) {
        padding-top: 79.83%;
      }
    }

    .img {
      position: absolute;
      right: 0;
      bottom: 0;
      left: 0;
      top: 0;
      transition: all 0.25s ease-in-out;

      /*@include breakpoints(s-large) {
        bottom: 0;
        left: 0;
        top: 0;
      }*/

      &.has-mobile-image {
        display: none;

        @include breakpoints(s-medium) {
          display: block;
        }
      }

      &.img-mobile {
        @include breakpoints(s-medium) {
          display: none;
        }
      }
    }
  }

  &:first-child {
    .content-wrap {
      @include breakpoints(s-large) {
        height: 190px;
      }
    }
  }

  &:nth-child(2) {
    .content-wrap {
      @include breakpoints(s-large) {
        height: 190px;
      }
    }
  }

  .content-wrap {
    flex: 1;
    display: flex;
    align-items: center;
    padding: rem(0 0 0 16);
    font-size: rem(14);
    line-height: 1.2;
    font-weight: 300;

    @include breakpoints(s-medium) {
      padding: rem(10 10 10 20);
      font-size: rem(16);
    }

    @include breakpoints(s-large) {
      min-height: 190px;
    }

    @include breakpoints(m-large) {
      padding: rem(10 30);
    }

    .tag {
      display: inline-block;
      vertical-align: top;
      background: $blue3;
      padding: rem(2 8);
      color: $white;
      font-size: rem(11);
      line-height: 1.23;
      text-transform: uppercase;
      margin-bottom: rem(8);
      font-weight: 700;

      @include breakpoints(s-medium) {
        padding: rem(3 12);
        font-size: rem(13);
      }

      &.bio {
        background-color: $bayside;
      }

      &.consumer {
        background-color: $pine;
      }

      &.clf {
        background-color: $cable_car;
      }

      &.crypto {
        background-color: $grapevine;
      }

      &.enterprise {
        background-color: $deep_ocean;
      }

      &.dynamism {
        background-color: $deep_ocean;
      }

      &.fintech {
        background-color: $marina;
      }

      &.lsv {
        background-color: $citrus;
      }

      &.podcast {
        background-color: $poppy;
      }

      &.summit {
        background-color: $poppy;
      }

      &.general {
        background-color: $poppy;
      }

      &.company {
        background-color: $poppy;
      }

        &.games {
            background-color: $games;
        }
    }

    // stylelint-disable
    h3 {
      font-size: rem(16);
      line-height: 1.21;
      margin-bottom: rem(4);
      font-weight: 500;
      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
      overflow: hidden;

      @include breakpoints(s-medium) {
        font-size: rem(24);
        margin-bottom: rem(11);
        font-weight: 700;
      }

      @include breakpoints(m-large) {
        font-size: rem(28);
      }

      @include breakpoints(none, s-medium) {
        -webkit-line-clamp: 2;
      }

      a {
        color: $steel;
        text-decoration: none;
      }
    }
    // stylelint-enable
  }

  .byline--post-list {
    // stylelint-disable
    @include breakpoints(none, s-medium) {
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
    // stylelint-enable

    a {
      font-weight: 300;
      color: $concrete;
    }
  }
}

