            margin-bottom: rem(20);
          }
        }
        > label {
          display: block;
          font-size: rem(13);
          line-height: 1.81;
          color: $steel;
          font-weight: 700;
          padding-left: rem(31);
          position: relative;
          width: 100% !important;
          cursor: pointer;

          @include breakpoints(s-medium) {
            font-size: rem(16);
            padding-left: rem(38);
          }
        }
        .mktoCheckboxList {
          position: absolute;
          left: 0;
          top: 3px;
          width: 18px;
          height: 18px;

          /*> label {
              position: absolute;
              left: 0;
              top: 0;
              width: 18px;
              height: 18px;
              border-radius: 2px;
              border: 1px solid $steel;
              margin: 0 !important;

              &:before {
                  transition: opacity 0.3s linear, visibility 0.3s linear;
                  content: "";
                  position: absolute;
                  left: 50%;
