                  border-width: 0 0 1px 1px;
                  margin-top: -1px;
                  opacity: 0;
                  visibility: hidden;
              }
          }*/

          input[type="checkbox"] {
            position: fixed;
            left: 0;
            top: 0;
            opacity: 0;
            z-index: -1;

            + label {
              position: absolute;
              left: 0;
              top: 0;
              width: 18px;
              height: 18px;
              border-radius: 2px;
              border: 1px solid $steel;
              margin: 0 !important;

              &:before {
                transition: opacity 0.3s linear, visibility 0.3s linear;
                content: "";
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%) rotate(-45deg);
                width: 9px;
                height: 4px;
                border: solid $steel;
                border-width: 0 0 1px 1px;
                margin-top: -1px;
                opacity: 0;
                visibility: hidden;
              }
            }

            &:checked + label:before {
              opacity: 1;
              visibility: visible;
            }
          }
        }
      }
    }
