.podcast-lists {
  padding: 0;
  margin: 0;
  list-style: none;

  > li {
    position: relative;
    padding: rem(0  16);

    @include breakpoints(s-medium) {
      position: static;
      padding: 0;
    }

    + li {
      margin-top: rem(20);
    }
  }

  .epi-detail {
    width: 100%;
    text-decoration: none;
    display: flex;
    align-items: center;
    font-size: rem(16);
    line-height: 1.2;
    font-weight: 300;
    color: $black;

    @include breakpoints(s-medium) {
      padding: rem(0 4);
    }

    &:hover {

      .play {
        background: $poppy;
      }
    }

    .holder {
      padding-left: rem(48);
      position: relative;

      @include breakpoints(s-large) {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        position: relative;
      }
    }

    .player-holder {
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      display: flex;
      flex-wrap: wrap;
      align-items: center;
    }

    .play {
      transition: background 0.3s linear;
      display: block;
      width: 32px;
      height: 32px;
      border-radius: 50%;
      background: $poppy;
      overflow: hidden;

      &::after {
        content: "\e90a";
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font: 12px/1 "icomoon";
        color: $white;
        margin-left: 2px;
      }
    }

    .text-wrap {
      flex: 1;
    }

    .epi {
      display: block;
      font-size: rem(16);
      line-height: 1.1875;
      font-weight: 300;
      margin-bottom: rem(0);
      position: relative;
      color: $concrete;
    }

    .title {
      display: block;
      font-weight: 600;
      font-size: rem(16);
      line-height: 1.4375;
      color: $steel;
    }

    .play-info {
      flex: 1;
      padding-left: rem(8);

      .player {
        position: relative;

        span {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          font-size: 11px;
          line-height: 1.18;
          font-weight: 700;
          color: $steel;

          &.start {
            left: 0;
          }

          &.end {
            left: auto;
            right: 0;
          }
        }
      }

      .foot {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: rem(2);
      }

      .speed {
        transition: color 0.3s linear, background 0.3s linear;
        display: inline-block;
        vertical-align: top;
        border: 2px solid $fog;
        min-width: 43px;
        text-align: center;
        padding: 3px;
        font-size: 11px;
        line-height: 1.18;
        font-weight: 700;
        color: $fog;
        text-decoration: none;

        &:hover {
          background: $fog;
          color: $white;
        }

      }

      ul {
        padding: 0;
        margin: 0;
        list-style: none;
        font-size: rem(11);
        line-height: 1.18;
        text-transform: uppercase;
        font-weight: 700;
        margin: 0 -4px;
        padding-left: rem(10);

        li {
          display: inline-block;
          vertical-align: middle;
          padding: 0 4px;

          a {
            transition: color 0.3s linear;
            color: $concrete;
            text-decoration: none;

            &:hover {
              color: $poppy;
            }
          }
        }
      }
    }
  }
}

.checklist {
  margin: rem(0 0 15);
  padding: 0;
  list-style: none;

  @include breakpoints(s-medium) {
    margin: rem(0 0 22);
  }

  li {

    + li {
      margin-top: rem(13);

      @include breakpoints(s-medium) {
        margin-top: rem(20);
      }
    }
  }

  input[type="checkbox"] {
    position: fixed;
    left: 0;
    top: 0;
    opacity: 0;
    z-index: -1;

    &:checked + .fake-input::before {
      opacity: 1;
      visibility: visible;
    }
  }

  label {
    display: block;
    padding-left: rem(31);
    position: relative;
    cursor: pointer;

    @include breakpoints(s-medium) {
      padding-left: rem(38);
    }
  }

  .title {
    display: block;
    font-size: rem(13);
    line-height: 1.81;
    color: $steel;
    font-weight: 700;

    @include breakpoints(s-medium) {
      font-size: rem(16);
    }
  }

  .fake-input {
    position: absolute;
    left: 0;
    top: 3px;
    width: 18px;
    height: 18px;
    border-radius: 2px;
    border: 1px solid $steel;

    &::before {
      transition: opacity 0.3s linear, visibility 0.3s linear;
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%) rotate(-45deg);
      width: 9px;
      height: 4px;
      border: solid $steel;
      border-width: 0 0 1px 1px;
      margin-top: -1px;
      opacity: 0;
      visibility: hidden;
    }
  }

  .fake-label {
    display: block;

    > span {
      display: block;
    }
  }
}

.visible-sm {
  @include breakpoints(s-large) {
    display: none;
  }
}

.hidden-sm {
  display: none;

  @include breakpoints(s-large) {
    display: block;
  }
}

.terms-inline {
  > span {
    &::after {
      content: ' | ';
    }

    &:last-of-type {
      &::after {
        content: none;
      }
    }
  }
}

@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }

  40% {
    transform: translateY(-16px);
  }

  60% {
    transform: translateY(-8px);
  }
}

.anim-bounce {
  position: relative;
  animation: bounce 2s infinite;
}

