  }

  + .home-main {
    padding-top: 0;
  }

  .container {
    margin: 0 auto;
  }

  .slider {
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;

    .slick-track,
    .slick-list {
      display: flex;
      width: 100%;
    }

    .slick-dots {
      @extend %listreset;
      padding-top: 16px;
      width: calc(100% + 8px);
