      z-index: 1;
      margin-right: auto;

      @include breakpoints(none, s-large) {
        padding: 30px 15px;
      }

      @include breakpoints(none, s-medium) {
        max-width: none;
      }
    }

    .logo-hold {
      padding: 43px 40px;

      @include breakpoints(none, s-large) {
