        }

        @include breakpoints(none, s-medium) {
          margin: 0;
        }
      }

      h1 {
        color: $steel;
      }

      .content-left {
        padding-top: 48px;
        padding-bottom: 48px;
        background: #f1faff;
        max-width: 350px;
        flex: 0 0 350px;
        //margin-right: 90px;

        @include breakpoints(none, s-large) {
